  border: 1px solid #ccc;
  border-radius: 3px;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="range"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="week"]:focus,
input[type="time"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="color"]:focus,
textarea:focus {
  color: #111;
}

select {
  border: 1px solid #ccc;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"] {
  padding: 3px;
}

textarea {
  padding-left: 3px;
  width: 100%;
}

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
a {
  color: royalblue;
}

a:visited {
  color: purple;
}

a:hover, a:focus, a:active {
  color: midnightblue;
}

a:hover, a:active {
  outline: 0;
}

/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
body #top-nav {
  text-align: right;
  -webkit-justify-content: flex-end !important;
  -moz-justify-content: flex-end !important;
  -ms-justify-content: flex-end !important;
  justify-content: flex-end !important;
  -ms-flex-pack: flex-end !important;
}

body #top-nav .common-nav ul, body #top-nav .common-nav ol {
  right: 0;
  left: auto;
}

.common-nav {
  text-align: left;
  margin-top: 10px;
  display: inline-block;
}

.common-nav li {
  position: relative;
}

.common-nav li a {
  color: #e9ebee;
  font-size: 14px;
  font-size: 0.875rem;
}

.common-nav li a:hover {
  text-decoration: none;
}

.common-nav li:hover > ul, .common-nav li:hover > ol {
  display: block;
}

.common-nav li ul, .common-nav li ol {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  min-width: 150px;
  padding-top: 15px;
  background: none;
  z-index: 999;
}

.common-nav li ul li a, .common-nav li ol li a {
  display: block;
  padding: 5px 10px;
  color: #00518A;
  background: #00518A;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  -webkit-transition: all 400ms ease;
  -moz-transition: all 400ms ease;
  -ms-transition: all 400ms ease;
  -o-transition: all 400ms ease;
  transition: all 400ms ease;
}

.common-nav li ul li a:hover, .common-nav li ol li a:hover {
  background: #00518A;
  color: #fff;
}

.common-nav li ul li:first-child a, .common-nav li ol li:first-child a {
  border: none;
}

.common-nav > li {
  float: left;
  margin: 0px 8px 0px 8px;
  margin: 0rem 0.5rem 0rem 0.5rem;
}

.common-nav > li a {
  padding-bottom: 7px;
}

.common-nav.main-nav {
  margin-left: 15%;
  background-color: #162564;
  border-right: 5px solid #09b2ea;
}

.common-nav.main-nav > li {
  margin: 0;
}

.common-nav.main-nav > li > a {
  padding: 5px 28px 5px 28px;
  padding: 0.3125rem 1.75rem 0.3125rem 1.75rem;
  border-left: 1px solid rgba(255, 255, 255, 0.5);
}

@media only screen and (max-width: 959px) {
  .common-nav.main-nav > li > a {
    margin: 0px 25px 0px 25px;
    margin: 0rem 1.5625rem 0rem 1.5625rem;
  }
}

.common-nav.main-nav > li > a:hover {
  background: #09b2ea;
}

.common-nav.main-nav > li:first-child > a {
  border-left: none;
}

.common-nav.main-nav > li a {
  /*font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;*/
  color: #e9ebee;
  font-size: 18px;
  font-size: 1.125rem;
}

.common-nav.main-nav ul, .common-nav.main-nav ol {
  min-width: 200px;
}

.common-nav.main-nav ul li a, .common-nav.main-nav ol li a {
  font-size: 16px;
  font-size: 1rem;
  -webkit-transition: all 400ms ease;
  -moz-transition: all 400ms ease;
  -ms-transition: all 400ms ease;
  -o-transition: all 400ms ease;
  transition: all 400ms ease;
}

.common-nav.main-nav ul li a:hover, .common-nav.main-nav ol li a:hover {
  background: #162564;
}

.bottom-header #primary-nav, .top-header #primary-nav {
  text-align: right;
}

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  color: #21759b;
  display: block;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
  outline: 0;
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
  display: inline;
  float: left;
}

.alignright {
  display: inline;
  float: right;
}

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clear:before,
.clear:after,
.entry-content:before,
.entry-content:after,
.comment-content:before,
.comment-content:after,
.site-header:before,
.site-header:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after {
  content: "";
  display: table;
  table-layout: fixed;
}

.clear:after,
.entry-content:after,
.comment-content:after,
.site-header:after,
.site-content:after,
.site-footer:after {
  clear: both;
}

/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/
.widget {
  margin: 0 0 1.5em;
  /* Make sure select elements fit in widgets. */
}

.widget select {
  max-width: 100%;
}

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Posts and pages
--------------------------------------------------------------*/
.sticky {
  display: block;
}

.hentry {
  margin: 0 0 1.5em;
}

.byline,
.updated:not(.published) {
  display: none;
}

.single .byline,
.group-blog .byline {
  display: inline;
}

.page-content,
.entry-content,
.entry-summary {
  margin: 1.5em 0 0;
}

.page-links {
  clear: both;
  margin: 0 0 1.5em;
}

/*--------------------------------------------------------------
## Asides
--------------------------------------------------------------*/
.blog .format-aside .entry-title,
.archive .format-aside .entry-title {
  display: none;
}

/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
.comment-content a {
  word-wrap: break-word;
}

.bypostauthor {
  display: block;
}

/*--------------------------------------------------------------
# Infinite scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
  /* Theme Footer (when set to scrolling) */
  display: none;
}

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before. */
.infinity-end.neverending .site-footer {
  display: block;
}

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%;
}

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
  margin-bottom: 1.5em;
  max-width: 100%;
}

.wp-caption img[class*="wp-image-"] {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.wp-caption .wp-caption-text {
  margin: 0.8075em 0;
}

.wp-caption-text {
  text-align: center;
}

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
  margin-bottom: 1.5em;
}

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%;
}

.gallery-columns-2 .gallery-item {
  max-width: 50%;
}

.gallery-columns-3 .gallery-item {
  max-width: 33.33%;
}

.gallery-columns-4 .gallery-item {
  max-width: 25%;
}

.gallery-columns-5 .gallery-item {
  max-width: 20%;
}

.gallery-columns-6 .gallery-item {
  max-width: 16.66%;
}

.gallery-columns-7 .gallery-item {
  max-width: 14.28%;
}

.gallery-columns-8 .gallery-item {
  max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
  max-width: 11.11%;
}

.gallery-caption {
  display: block;
}

a:visited {
  color: #00518A;
}

* {
  -webkit-font-smoothing: antialiased;
}

ul, ol {
  margin: 0;
}

ul[class*='-lists'], ol[class*='-lists'] {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.margin-none {
  margin: 0;
}

@media only screen and (max-width: 1199px) {
  .container {
    width: 100%;
  }
}

a {
  -webkit-transition: all 0.42s ease-in-out;
  -o-transition: all 0.42s ease-in-out;
  transition: all 0.42s ease-in-out;
  display: inline-block;
}

.box-shadows {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.29);
  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.29);
}

.form-control::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  font: 400 18px/30px 'Roboto';
  color: #D89019;
  text-transform: capitalize;
}

.form-control::-moz-placeholder {
  /* Firefox 19+ */
  font: 400 18px/30px 'Roboto';
  color: #D89019;
  text-transform: capitalize;
}

